@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-SemiBold.ttf") format("truetype");
    font-weight: 600;
    font-style: normal;
  }

  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-Medium.ttf") format("truetype");
    font-weight: 500;
    font-style: normal;
  }

  @font-face {
    font-family: "Montserrat";
    src: url("./assets/fonts/Montserrat-Regular.ttf") format("truetype");
    font-weight: 400;
    font-style: normal;
  }

  html,
  body {
    @apply bg-body font-montserrat;
  }

  header {
    @apply bg-main text-white px-7 py-4 h-[10vh];
    @apply fixed w-full top-0 left-0 right-0 flex-between z-40;
  }

  aside {
    @apply fixed h-[90vh] w-[150px] top-[10vh] bg-main text-white;
  }

  /* Form */
  label {
    @apply w-full font-medium text-lg my-5;
  }

  input,
  select,
  textarea {
    @apply w-full bg-transparent outline-none p-2 px-5;
    @apply text-black rounded-10 placeholder:text-black-rgba border border-dark-rgba;
  }

  button {
    @apply font-medium text-lg text-white;
  }

  /* ------ Text styles ------ */
  h1 {
    @apply font-semibold text-[20px] leading-6;
  }

  h2 {
    @apply font-semibold text-2xl;
  }

  h3 {
    @apply font-medium text-sm;
  }

  p {
    @apply text-lg;
  }

  strong {
    @apply font-medium text-2xl text-center;
  }

  span {
    @apply font-semibold text-lg !align-[0px];
  }

  button {
    @apply text-white font-medium text-md leading-6;
  }

  time {
    @apply font-semibold text-lg;
  }

  /* Table */
  table {
    @apply w-full;
  }

  thead {
    @apply w-full font-semibold text-lg border-light-rgba border-b-2;
  }

  tbody {
    @apply w-full text-lg;
  }

  td {
    @apply py-3;
  }

  tr {
    @apply py-5;
  }
}

@layer components {
  .container {
    @apply max-w-full ml-36 pb-10 py-8 px-10;
  }

  .flex-center {
    @apply flex items-center;
  }

  .flex-items-center {
    @apply flex items-center justify-center;
  }

  .flex-between {
    @apply flex items-center justify-between;
  }

  /* Header */
  .search-thumb {
    @apply bg-main absolute right-0 top-[50%] p-1;
    @apply rounded-10 translate-y-[-50%] translate-x-[-20%];
  }

  /* Buttons */
  .main-btn {
    @apply flex-center gap-x-2 px-5 py-[9px] rounded-5;
  }

  /* Table  */
  .table-border {
    @apply border-dark-rgba border-b;
  }

  /* Progress Bar  */
  .progress-bar {
    @apply flex-items-center w-7 h-7 absolute top-0 bg-dark-blue rounded-full transition duration-1000 ease-in-out;
  }

  /* Checkbox */
  .work-checkbox {
    @apply cursor-pointer absolute w-10 h-1 bg-gray block m-0;
    @apply rounded-full top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2;
  }

  .work-checkbox:after {
    @apply content-[""] absolute top-1/2 left-0 -translate-y-1/2 w-5 h-5 bg-gray rounded-full duration-300;
  }

  .work-checkbox-active {
    @apply bg-save after:bg-save after:left-[calc(100%-20px)];
  }

  .border-round {
    @apply border border-dark-rgba px-5 rounded-10 text-md flex-between;
  }

  /* Error */
  .error {
    @apply text-cancel absolute text-sm mt-1;
  }

  /* And */
  .ant-progress-line {
    padding: 0 !important;
    margin-inline-end: 0px !important;
    margin-bottom: 0px !important;
  }

  .progress-list > div {
    padding: 0 !important;
    margin: 0 !important;
  }

  .progress-list > div > div {
    padding: 0 !important;
    margin: 0 !important;
  }

  .ant-picker-ok > button,
  [type="button"] {
    @apply bg-[#1677ff];
  }

  .ant-picker-ok > button,
  [type="button"] > span {
    @apply font-montserrat font-normal text-sm;
  }
}
